<template>
<div class="form-group mt-3">
    <label>
        {{ label }}
        <!-- <span v-if="activated"
            :class="valid ? 'fa fa-check-circle text-success' : 'fa fa-exclamation-circle text-danger'"
        ></span> -->
        <template v-if="activated">
            <img v-if="valid" class="myIcon" src="/img/iconTrue.jpg"/>
            <img v-else class="myIcon" src="/img/iconFalse.png" />
        </template>
    </label>
    <input type="text" class="form-control" :value="value" @input="onInput">
</div>
</template>

<script>
export default {
    props: {
        label: {
            type: String,
            required: true
        },
        value: {
            type: String,
            required: true
        },
        valid: {
            type: Boolean,
            required: true
        }
    },
    data() {
        return {
            activated: this.value != ''
        }
    },
    methods: {
        onInput(e){
            this.activated = true;
            const value = e.target.value;
            this.$emit('updated', value)
        },
    },
}
</script>
<style>
.myIcon{
    width: 25px;
    height: 25px;
    margin-left: 5px;
}
</style>